header {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.grid-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
    grid-gap: 30px;

    article {
        position: relative;
        display: block;
    }
}

footer {
    .sitemap {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
        grid-gap: 20px;
    }

    .contact {
        display: grid;
        grid-gap: 20px;

        .footer-links {
            position: relative;
            display: block;
        }
    }
}

@media only screen and (min-width: 992px) {
    /* Grid for desktop */
    footer {
        .contact {
            grid-template-columns: 570px auto auto;
        }
    }
}

@media only screen and (max-width: 992px) {
    /* Grid for tablets */
    footer {
        .contact {
            grid-template-columns: 100%;
        }
    }
}

@media only screen and (max-width: 768px) {
    /* Grid for mobile phones */
    footer {
        .contact {
            grid-template-columns: 100%;
        }
    }
}